import React, { Component } from 'react';
import './components.css';
import {Drawer} from "antd";
import Markdown from "markdown-to-jsx";


class About extends Component {

  constructor(props){
    super(props);
    this.state = {
        visible: false
    }
  }

  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    //const {caption} = this.state;
    const pic = require("../pics/fancy_me.jpg");
    return (
        <div className="navItemContainer">
        <button className="smallName navitem" onClick={this.showDrawer}>
            About Me
        </button>
        <Drawer
          title="About Me"
          placement="left"
          closable={true}
          width = "50%"
          onClose={this.onClose}
          visible={this.state.visible}
         
        >
          <div className="blog">
            <div className="title">
                Hello there friend!
            </div>
            <img src={pic} className="picture"/>
            <div className="date"><i>Written by Bryan Moh || 24.3/2019</i></div>
            <div className="text">
                <p>
                Not too sure how you found this page (I haven't told anyone about this site yet except for a few special people so...)
                but welcome anyway.  If you were looking for someone to hire or need a shoulder to cry on please be advised that I charge
                for both services and yes I am available to start immediately.  
                </p>
                <p>
                My number is 04561- lol jks I wouldn't tell you that.
                </p>
                Take care!
            </div>
          </div>
        </Drawer>

        </div>
    )
  }
}

export default About;
